.diving {
	position: relative;
	padding-top: rem(152);

	padding-bottom: rem(225);

	@include min1280 {
		display: flex;
		flex-direction: column;
	}

	@include min768 {
		padding-top: rem(84);
		padding-bottom: rem(100);
	}

	.top {
		@include min1280 {
			margin-left: 20px;
			margin-right: 20px;
		}
	}

	.section_header {
		.title-h2 {
			margin-bottom: rem(32);

			@include min768 {
				margin-bottom: rem(16);
			}
		}

		.text-p {
			margin-top: rem(16);
			max-width: rem(568);

			@include min768 {
				max-width: 100%;
			}
		}
	}

	.grid_slider {
		position: relative;
		margin-top: rem(78);
		margin-left: rem(184);
		display: grid;
		grid-template-columns: calc((100% - 1145px)/2) 0fr 10fr calc((100% - 1145px)/2);

		@include min1280 {
			display: flex;
			margin-left: rem(20);
			// max-width: 1145px;
			// margin: 0 auto;
		}

		@include min540 {
			margin-right: rem(20);
			margin-top: rem(24);
		}

		.buffer {
			grid-column: span 1;

			.slider_bullet {
				position: absolute;
				top: rem(20);
				left: rem(500);
				z-index: 3;

				display: flex;
				align-items: center;

				@include min1280 {
					left: 0;
					top: rem(346);
				}

				@include min540 {
					top: rem(336);
				}

				.item {
					font-size: rem(14);
					color: #E2E2E2;
					margin-right: rem(20);
					display: flex;
					align-items: center;

					&:last-child {
						margin-right: 0;

						&::after {
							display: none;
						}
					}
				}

				.item.active {
					color: var(--c-text);
					margin-right: 0;

					&::after {
						content: "";
						flex: 0 0 rem(33);
						width: rem(33);
						height: rem(1);
						background-color: #E2E2E2;
						margin: 0 rem(14);
						// display: block;
					}
				}
			}
		}
		.slider {
			grid-column: 2 / sp;

			@include min1280 {
				min-width: 0px;
			}

			.swiper-slide {
				opacity: .3;
			}
			.swiper-slide-active {
				opacity: 1;
			}
		}
	}

	&__control {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%);
		z-index: 2;

		width: 100%;
		display: flex;
		justify-content: space-between;

		@include min1280 {
			position: relative;
			top: auto;
			left: auto;
			transform: translateX(0%);
			width: auto;
			order: 10;

			margin-top: rem(30);
		}

		.bullet_slide {
			display: none;

			@include min1280 {
				display: flex;
			}

			.swiper-pagination-bullet {
				background-color: rgba(127, 139, 155,.2) !important;
			}

			.swiper-pagination-bullet-active {
				background-color: var(--c-blue) !important;
			}
		}

		.slider_bullet {
			position: absolute;
			top: rem(-70) !important;
			left: 51% !important;
			display: none;

			@include min1280 {
				position: relative;
				top: auto;
				left: auto;
			}

			.swiper-pagination-bullet {
				// background-color: transparent !important;
				// display: flex !important;
				// align-items: center;
				// font-size: rem(14);
				// color: #E2E2E2;
				// opacity: 1 !important;

				// width: auto !important;
				// height: auto !important;

				// margin: 0 rem(20) 0 0 !important;

				// transition: var(--transition) !important;

				// &:hover {
				// 	color: var(--c-text);
				// }
				
				// &:last-child {
				// 	&::after {
				// 		display: none;
				// 	}
				// }
				
			}

			.swiper-pagination-bullet-active {
				// color: var(--c-text);
				// margin: 0 !important;

				// &::after {
				// 	content: "";
				// 	flex: 0 0 rem(33);
				// 	width: rem(33);
				// 	height: rem(1);
				// 	background-color: #E2E2E2;
				// 	margin: 0 rem(14);
				// }
			}
		}

		.button_slide_prev, .button_slide_next {
			position: absolute;
			top: rem(32);

			@include min1280 {
				position: relative;
				top: auto;
			}
		}

		.button_slide_prev {
			left: 0;

			@include min1280 {
				order: -1;
			}
		}

		.button_slide_next {
			right: 0;
		}
	}

	&__item {
		display: flex;
		max-width: rem(957);

		@include min1280 {
			flex-wrap: wrap;
			max-width: 100%;
		}

		&_left {
			flex: 0 0 rem(460);
			max-width: rem(460);
			height: rem(306);
			margin-right: rem(30);

			@include min1050 {
				flex: 0 0 100%;
				max-width: 100%;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&_right {
			display: flex;
			flex-direction: column;
			margin-top: rem(98);

			@include min540 {
				margin-top: rem(72);
			}

			.title {
				color: var(--c-blue);
				font-size: rem(21);
				line-height: 150%;
				font-family: var(--f-prata);
				margin-bottom: rem(16);
			}

			.text-p {

			}
		}
	}
}

