.advantages {
	position: relative;
	height: rem(776);
	background: url(../img/section3/image.jpg) no-repeat;

	padding-top: rem(148);

	@include min768 {
		background: none;
		height: auto;
		padding-top: 0;
	}

	.container {
		position: relative;

		@include min768 {
			margin-left: 0;
			margin-right: 0;
		}
	}
	
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(68, 51, 119, .93);
		z-index: 0;

		@include min768 {
			display: none;
		}
	}

	&__left, &__right {
		position: relative;
		z-index: 1;

		@include min768 {
			padding-left: 20px;
			padding-right: 20px;
		}
	}

	&__left {
		// margin-top: rem(148);

		img {
			height: rem(500);

			@include min768 {
				height: auto;
				width: 100%;
				object-fit: cover;
			}
		}
	}

	&__right {
		position: absolute;
		top: rem(100);
		right: rem(70);
		width: rem(465);
		
		@include min768 {
			position: relative;
			top: auto;
			right: auto;
			width: auto;

			background-color: white;

			padding: rem(42) rem(20) 0 rem(20);
		}
		

		&_wrap {
			padding: rem(50) rem(70);
			background-color: white;

			@include min768 {
				padding: 0;
			}
		}

		&_nav {
			margin-top: rem(12);

			display: flex;
			align-items: center;
			justify-content: flex-end;

			@include min768 {
				margin-top: rem(28);
				justify-content: space-between;
			}

			.bullet_slide {
				margin-right: rem(58);

				@include min768 {
					margin-right: 0;

					.swiper-pagination-bullet {
						background-color: rgba(127, 139, 155,.2) !important;
					}

					.swiper-pagination-bullet-active {
						background-color: var(--c-blue) !important;
					}
				}
			}

			.button_slide_prev {
				margin-right: rem(16);

				@include min768 {
					order: -1;
				}
			}
		}

		&_count {
			position: absolute;
			top: rem(-45);
			right: rem(-12);

			width: rem(80);
			height: rem(80);
			background-color: var(--c-green);

			display: flex;
			align-items: center;
			justify-content: center;

			@include min768 {
				width: rem(60);
				height: rem(60);

				top: rem(-35);
				right: rem(30);
			}

			.count {
				font-size: rem(24);
				color: white;
				font-family: var(--f-prata);

				&::before {
					content: "0";
				}

				@include min768 {
					font-size: rem(18);
				}
			}
		}

		&_title {
			margin-bottom: rem(28);

			@include min540 {
				margin-bottom: rem(20);
			}

			.title-h2 {
				color: var(--c-blue2);
				margin-bottom: rem(16);

				@include min540 {
					margin-bottom: 0;
				}
			}
		}

		.swiper-slide {
			b {
				display: block;
				margin-bottom: rem(12);

				font-weight: 600;
				font-size: rem(16);
				line-height: 150%;
			}

			ul {
				li {
					line-height: 130%;
					font-size: rem(16);
				}
			}

			.list {
				li {
					margin-bottom: rem(12);
				}
			}
		}
	}
}