.section_card {
	position: relative;

	padding-bottom: rem(190);

	@include min768 {
		padding-bottom: rem(90);
	}

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		right: 0;
		background: url(../img/section2/right_tree.png) no-repeat;
		width: rem(696);
		height: rem(568);

		@include min1050 {
			display: none;
		}
	}

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		background: url(../img/section2/left_tree.png) no-repeat;
		width: rem(544);
		height: rem(906);
		z-index: -1;

		@include min1050 {
			display: none;
		}
	}

	.bullet_slide {
		display: none;

		@include min768 {
			margin-top: rem(37);
			display: flex;
			justify-content: center;

			.swiper-pagination-bullet {
				background-color: rgba(127, 139, 155,.2) !important;
			}

			.swiper-pagination-bullet-active {
				background-color: var(--c-blue) !important;
			}
		}
	}

	.container {
		@include min768 {
			display: flex;
			flex-direction: column;
		}
	}

	.section_header {
		margin-bottom: rem(54);

		svg {
			display: none;

			@include min768 {
				display: block;
				margin-top: rem(14);
			}
		}

		@include min1050 {
			margin-bottom: rem(32);

			br {
				display: none;
			}
		}
	}

	&__image {
		height: rem(543);
		margin-bottom: rem(77);

		@include min768 {
			order: -1;
			height: rem(313);
			margin-bottom: rem(46);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__item {
		display: flex;

		.image {
			margin-right: rem(46);

			@include min768 {
				max-width: rem(40);
				margin-right: rem(18);

				svg {
					max-width: rem(40);
				}
			}
		}


	}

	.swiper {
		.swiper-wrapper {

			@include max768 {
				flex-wrap: wrap;
				justify-content: space-between;

				.swiper-slide {
					margin-bottom: rem(83);

					&:first-child, &:nth-child(3) {
						flex: 0 0 rem(574);
						max-width: rem(574);
					}

					&:nth-child(2), &:nth-child(4) {
						flex: 0 0 rem(443);
						max-width: rem(433);
					}

					&:last-child {
						flex: 0 0 rem(562);
						max-width: rem(562);
					}
				}
			}
			
		}
	}
}