.info {
	position: relative;
	padding-top: rem(143);
	padding-bottom: rem(164);

	@include min1200 {
		display: flex;
		flex-direction: column;
	}

	@include min768 {
		padding-top: rem(93);
		padding-bottom: rem(70);
	}

	&::after {
		content: "";
		position: absolute;
		right: rem(-200);
		bottom: rem(-285);
		background: url(../img/section4/right_bg.png) no-repeat;
		width: rem(1263);
		height: rem(938);

		@include min1440 {
			right: rem(-280);
		}

		@include min1200 {
			// position: relative;
			// right: auto;
			// bottom: auto;
			// background: url(../img/section4/right_bg_smart.png) no-repeat;
			// width: 100%;
			// max-width: rem(750);
			// height: 100%;

			display: none;
		}
	}

	.container {
		@include min1200 {
			margin-left: 0;
			margin-right: 0;
		}
	}

	.image_smart {
		display: none;

		@include min1200 {
			display: block;

			width: 100%;
			height: 100%;
			object-fit: cover;

			min-height: rem(336);
		}
	}

	.section_header {
		margin-bottom: rem(28);

		@include min1200 {
			padding-left: 20px;
			padding-right: 20px;
		}

		.title-h2 {
			margin-bottom: rem(42);

			@include min768 {
				margin-bottom: 0;

				br {
					display: none;
				}
			}
		}

		svg {
			@include min768 {
				display: none;
			}
		}
	}

	.subtitle {
		font-weight: 600;
		margin-bottom: rem(36);
		max-width: rem(415);
		display: inline-block;
		line-height: 150%;

		@include min1200 {
			max-width: 100%;
			padding-left: 20px;
			padding-right: 20px;
		}

		@include min768 {
			font-size: rem(15);
		}
	}

	.list {
		display: flex;
		flex-direction: column;

		max-width: rem(495);

		margin-bottom: rem(63);

		@include min1200 {
			max-width: 100%;
			padding-left: 20px;
			padding-right: 20px;
		}

		li {
			margin-bottom: rem(38);

			display: flex;

			@include min768 {
				margin-bottom: rem(30);

				br {
					display: none;
				}
			}

			&:last-child {
				margin-bottom: 0;
			}

			.image {
				margin-right: rem(35);

				flex: 0 0 rem(47);
				max-width: rem(47);

				display: flex;
				justify-content: center;

				@include min768 {
					margin-right: rem(28);

					flex: 0 0 rem(36);
					max-width: rem(36);
				}
			}

		
		}
	}

	.button_green {
		&:hover {
			span {
				color: var(--c-green);
			}
		}

		@include min1200 {
			margin-left: auto;
			margin-right: auto;

			margin-bottom: rem(50);
		}
	}

	

}

.info__bottom {
	position: relative;
	// margin-top: rem(164);

	.section_header {
		margin-bottom: rem(28);

		@include min768 {
			margin-bottom: rem(26);
		}

		.title-h2 {
			margin-bottom: rem(42);

			@include min768 {
				margin-bottom: rem(16);
			}
		}
	}

	.text-p {
		padding-bottom: rem(50);
		max-width: rem(502);

		@include min768 {
			padding-bottom: rem(32);
			max-width: 100%;

			br {
				display: none;
			}
		}
	}
}


.map {
	position: relative;
	height: rem(563);
	overflow-x: auto;

	@include min1200 {
		picture {
			position: absolute;
			top: 0;
			left: 0;
			width: 1200px;
		}
	}

	@include min768 {
		height: rem(398);
		overflow: hidden;

		picture {
			position: relative;
			top: 0;
			left: 0;
			width: 100%;
		}
	}

	img {
		width: 100%;
		height: rem(563);
		object-position: top;
		object-fit: cover;
		vertical-align: middle;

		@include min768 {
			height: 100%;

			width: 100%;
			object-position: top left;
		}

		@include min540 {
			object-position: -216px;
		}
	}


	.container {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 100%;
		height: 100%;
	}

	&__item {
		position: absolute;
		top: rem(136);
		left: rem(500);
		display: flex;
		flex-direction: column;
		align-items: center;

		@include min768 {
			top: rem(125);
			left: rem(305);
		}

		@include min540 {
			left: rem(90);
		}

		svg {
			@include min768 {
				width: rem(28);
				height: rem(35);
			}
		}

		span {
			background-color: var(--c-blue);

			padding: rem(15) rem(30);

			font-family: var(--f-prata);
			color: white;

			@include min768 {
				padding: rem(10) rem(20);
				font-size: rem(13);
			}
		}
	}

}