.header {
	position: relative;
	height: rem(814);

	background: url(../img/header_bg.jpg) no-repeat;
	background-size: cover;

	margin-bottom: rem(126);

	@include min768 {
		background: url(../img/header_bg_smart.jpg) no-repeat;
		background-size: cover;

		margin-bottom: rem(90);
	}

	&::after {
		content: "";
		position: absolute;
		bottom: rem(-110);
		right: 0;
		background: url(../img/header_tree.png) no-repeat;
		width: rem(146);
		height: rem(364);

		@include min1050 {
			display: none;
		}
	}

	.container {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	&__top {
		display: flex;
		justify-content: space-between;
		padding-top: rem(25);

		@include min768 {
			padding-top: rem(20);
		}

		&_burger {
			display: flex;
			align-items: center;

			.left {
				width: rem(56);
				height: rem(56);
				border-radius: 50%;
				background-color: var(--c-green);

				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;

				margin-right: rem(17);

				@include min768 {
					margin-right: 0;
				}

				.line {
					width: rem(20);
					height: rem(1);
					background-color: white;
					margin-bottom: rem(4);

					&:last-child {
						margin-bottom: 0;
					}
				}
			}

			.right {
				font-size: rem(12);
				color: white;
				line-height: 140%;
				text-transform: uppercase;

				@include min768 {
					display: none;
				}
			}
		}

		&_right {
			display: flex;
			align-items: center;

			@include min768 {
				display: none;
			}

			.phone {
				display: flex;
				align-items: center;
				margin-right: rem(28);

				svg {
					margin-right: rem(12);
				}

				span {
					color: white;
				}
			}
		}
	}

	&__center {
		display: flex;
		flex-direction: column;
		align-items: center;
		
		@include min540 {
			padding-left: rem(12);
			padding-right: rem(12);
		}
		
		.title-h1 {
			color: white;
			margin-bottom: rem(8);
		}

		.text-p {
			margin-bottom: rem(55);
			color: white;

			@include min768 {
				max-width: rem(270);
				text-align: center;
			}
		}

		.button_border_green, .button_green {
			@include min540 {
				width: 100%;
			}
		}

		.button_border_green {
			display: none;

			@include min768 {
				display: block;
				
				border-color: white;
				margin-top: rem(16);

				span {
					color: white;
				}
			}
		}
	}

	&__bottom {
		display: flex;
		justify-content: center;
		padding-bottom: rem(70);

		@include min540 {
			padding-left: rem(12);
			padding-right: rem(12);
		}
		
		.item {
			display: flex;
			flex-direction: column;

			margin-right: rem(140);

			@include min768 {
				align-self: flex-start;
				margin-right: rem(25);
				max-width: rem(98);

				&:nth-child(2) {
					order: -1;
				}
			}

			&:last-child {
				margin-right: 0;
			}

			&__top {
				font-size: rem(48);
				color: white;
				font-family: var(--f-prata);
				font-weight: normal;

				@include min768 {
					font-size: rem(32);
				}
			}

			&__bottom {
				font-size: rem(14);
				color: white;
				text-transform: lowercase;

				@include min768 {
					font-size: rem(13);
				}
			}
		}
	}
}