.layouts {
	position: relative;
	padding-top: rem(110);
	padding-bottom: rem(210);

	@include min768 {
		padding-top: rem(84);
		padding-bottom: rem(89);
	}

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;

		background: url(../img/section6/left_tree.png) no-repeat;
		width: rem(317);
		height: rem(618);
		z-index: -1;
		
		@include min1440 {
			display: none;
		}
	}

	.section_header {
		margin-top: rem(68);

		@include min768 {
			margin-top: 0;
		}
		
		.title-h2 {
			margin-bottom: rem(32);

			@include min540 {
				margin-bottom: rem(16);
			}
		}

		.text-p {
			margin-top: rem(16);
			max-width: rem(346);

			@include min1050 {
				max-width: 100%;
			}
		}
	}

	.grid_slider {
		display: grid;
		grid-template-columns: calc((100% - 1145px)/2) 0fr 10fr calc((100% - 1145px)/2);

		@include min1050 {
			position: relative;
			display: flex;
		}

		.buffer {
			grid-column: span 1;
		}
		.slider {
			grid-column: 2 / sp;

			display: flex;
			
			@include min1200 {
				margin-left: 20px;
			}

			@include min1050 {
				min-width: 0;
				flex: 0 0 100%;
				max-width: 100%;
				flex-wrap: wrap;
			}

			.swiper {
				@include min1050 {
					flex: 0 0 100%;
					max-width: 100%;

					height: rem(380);
					position: absolute;
					top: rem(128);
					left: 0;
				}
			}

			.swiper-slide {
				opacity: .3;

				align-items: flex-end;

				@include min768 {
					justify-content: center;
					align-items: center;
					display: flex;
					flex-direction: column;
				}

				.layouts__item_info {
					display: none;
				}
			}
			.swiper-slide-active {
				opacity: 1;

				.layouts__item_info {
					display: flex;
				}
			}
		}
	}
	
	.container {
		position: relative;
	}

	&__nav {
		position: absolute;
		z-index: 2;
		left: rem(725);
		top: rem(-110);

		display: flex;
		align-items: center;

		@include min1050 {
			display: none;
		}

		.button_slide_prev {
			margin-right: rem(16);
		}
	}

	&__left {
		flex: 0 0 rem(346);
		max-width: rem(346);
		margin-right: rem(140);

		@include min1050 {
			margin-right: 0;
			flex: 0 0 100%;
			max-width: 100%;

			> svg {
				display: none;
			}
		}

		.bullet_slide {
			margin-top: rem(40);
			margin-bottom: rem(120);

			@include min1050 {
				display: flex;
				justify-content: center;
				margin-top: rem(430);
				margin-bottom: rem(82);
			}

			.swiper-pagination-bullet {
				background-color: rgba(127, 139, 155,.2) !important;
			}

			.swiper-pagination-bullet-active {
				background-color: var(--c-blue) !important;
			}
		}

		&_bottom {
			margin-top: rem(28);
			display: flex;
			flex-direction: column;

			@include min540 {
				position: relative;
				padding-left: rem(22);

				&::after {
					content: "";
					position: absolute;
					bottom: rem(20);
					right: 0;
					background: url(../img/section6/left_tree_smart.png) no-repeat;
					width: rem(89);
					height: rem(212);
				}
			}

			b {
				font-size: rem(21);
				line-height: 150%;
				margin-bottom: rem(32);

				@include min540 {
					font-size: rem(16);
					margin-bottom: rem(16);
				}
			}

			p {
				font-size: rem(14);
				line-height: 150%;
				margin-bottom: rem(21);
				max-width: rem(152);

				@include min540 {
					margin-bottom: rem(40);
				}
			}

			.button_green {
				align-self: flex-start;

				&:hover {
					span {
						color: var(--c-green);
					}
				}
			}
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		height: 100%;
		width: rem(420);

		@include min1050 {
			width: auto;
		}

		@include min768 {
			align-items: center;
		}

		.image {
			max-width: rem(420);
			height: rem(548);

			@include min1050 {
				max-width: 100%;
				height: rem(324);

				display: flex;
				align-items: center;
				justify-content: center;
			}
		}
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;

			@include min1050 {
				width: auto;
				height: 100%;
			}
		}

		&_info {
			padding: rem(30) rem(25);
			background-color: var(--c-blue);

			display: flex;
			align-items: center;

			@include min1050 {
				padding: rem(22) rem(50);
				justify-content: center;
			}

			@include min768 {
				width: rem(248);
				padding: rem(22) 0;
			}

			.item {
				display: flex;
				align-items: center;
				margin-right: rem(28);

				@include min1050 {
					margin-right: rem(20);
				}

				&:last-child {
					margin-right: 0;
				}

				

				span {
					font-size: rem(14);
					color: white;
					margin-left: rem(8);
				}
			}
		}
	}
}