.footer {
	background-color: var(--c-blue);
	padding-bottom: rem(42);
	padding-top: rem(57);

	@include min1050 {
		padding-top: rem(40);
		padding-bottom: rem(34);
	}

	.container {
		display: flex;
		flex-direction: column;
	}

	&__top {
		padding-bottom: rem(48);
		border-bottom: rem(1) solid rgba(255,255,255,.14);
		margin-bottom: rem(24);

		@include min1050 {
			display: none;
		}

		ul {
			display: flex;
			justify-content: space-between;

			li {

				a {
					font-size: rem(12);
					font-weight: 500;
					line-height: 148%;
					text-transform: uppercase;
					color: white;
				}
			}
		}
	}

	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@include min1050 {
			flex-direction: column;

			.logo {
				margin-bottom: rem(30);
			}
		}

		span {
			font-size: rem(12);
			color: white;

			@include min1050 {
				margin-bottom: rem(8);

				&:last-child {
					margin-bottom: 0;
				}
			}

			@include min768 {
				color: rgba(255,255,255,.5);
			}
		}
	}
}