.message {
	position: relative;
	padding-bottom: rem(120);
	background: url(../img/section8/bg.jpg) no-repeat;
	background-size: cover;
	
	@include min1050 {
		background: none;
		padding-bottom: 0;
	}

	&::after {
		content: "";
		position: absolute;
		right: 0;
		bottom: 0;
		background: url(../img/section8/right_tree.png) no-repeat;
		width: rem(942);
		height: rem(537);

		@include min1050 {
			display: none;
		}
	}

	.container {
		display: flex;
		// position: relative;

		@include min1050 {
			flex-wrap: wrap;

			margin-left: 0;
			margin-right: 0;
		}
	}

	&__left {
		margin-top: rem(72);
		margin-right: rem(188);

		display: flex;
		flex-direction: column;

		@include min1050 {
			position: relative;
			flex: 0 0 100%;
			max-width: 100%;
			margin-right: 0;
			margin-top: rem(42);
			// margin-bottom: rem(32);

			padding-bottom: rem(75);

			background: url(../img/section8/bg_smart.jpg) no-repeat;
			background-size: cover;

			padding-left: 20px;
			padding-right: 20px;

			&::after {
				content: "";
				position: absolute;
				bottom: 0;
				right: 0;
				background: url(../img/section8/right_tree_smart.png) no-repeat;
				width: rem(146);
				height: rem(198);
			}
		}

		.section_header {
			margin-bottom: rem(50);

			.title-h2 {
				margin-bottom: rem(34);
			}
		}

		.item {
			display: flex;
			align-items: center;
			align-self: flex-start;
			margin-bottom: rem(30);

			&:last-child {
				margin-bottom: 0;
			}

			&__image {
				width: rem(38);
				height: rem(38);
				flex: 0 0 rem(38);
				border-radius: 50%;

				display: flex;
				align-items: center;
				justify-content: center;

				background-color: var(--c-green);

				margin-right: rem(30);
			}

			span {
				@include min540 {
					font-size: rem(16);
					margin-right: rem(16);
				}
			}
		}
	}

	&__right {
		position: relative;
		z-index: 2;

		@include min1050 {
			display: flex;
			justify-content: center;
			width: 100%;

			
		}
		
		.form {
			width: rem(460);
			padding: rem(64) rem(50);
			background-color: white;
			box-shadow: 0px 12px 21px -15px rgba(102, 102, 102, 0.25);

			@include min1050 {
				width: 100%;
				padding: rem(54) rem(38);
				box-shadow: none;

				display: flex;
				flex-direction: column;
			}

			&__title {
				margin-bottom: rem(32);
				line-height: 150%;
				font-size: rem(21);
				font-weight: 600;

				@include min540 {
					font-size: rem(18);
					
					margin-left: auto;
					margin-right: auto;
				}
			}

			.field-set {
				margin-bottom: rem(40);

				&:last-child {
					margin-bottom: rem(36);
				}
			}

			.field-button {
				> span {
					font-size: rem(12);
					line-height: 130%;
					color: #97A3B4;
					max-width: rem(228);
					display: block;

					@include min540 {
						margin-left: auto;
						margin-right: auto;
						text-align: center;
					}
				}
			}

			.button_green {
				margin-bottom: rem(26);
				width: 100%;

				&:hover {
					span {
						color: var(--c-green);
					}
				}
			}
		}
	}
}